$mqs: (
  //name breakpoint
  'mobile-sm': 320,
  'mobile-s': 375,
  'mobile-sl': 475,
  'mobile-md': 576,
  'mobile-l': 768,
  'tablet': 992,
  'desktop-sm': 1100,
  'desktop-med': 1280,
  'desktop-lg': 1440,
  'desktop-xl': 1921
);

@mixin for-width($size, $size2: null) {

  @each $name, $bp in $mqs {
    $max: calc(#{em($bp)} - #{em(1)});
    $min: em($bp);

    @if $size == "-#{$name}" {
      @media (max-width: $max) {
        @content;
      }
    }

    @if $size == "+#{$name}" {
      @media (min-width: $min) {
        @content;
      }
    }

    @if $size2 == "#{$name}" {

      @each $name, $bp in $mqs {
        $min: em($bp);

        @if $size == "#{$name}" {
          @media (min-width: $min) and (max-width: $max) {
            @content;
          }
        }
      }
    }
  }
}

