@mixin for-height($size, $size2: null) {

  @each $name, $bp in $mqs {
    $max: calc(#{em($bp)} - #{em(1)});
    $min: em($bp);

    @if $size == "-#{$name}" {
      @media (max-height: $max) {
        @content;
      }
    }

    @if $size == "+#{$name}" {
      @media (min-height: $min) {
        @content;
      }
    }

    @if $size2 == "#{$name}" {

      @each $name, $bp in $mqs {
        $min: em($bp);

        @if $size == "#{$name}" {
          @media (min-height: $min) and (max-height: $max) {
            @content;
          }
        }
      }
    }
  }
}

